import { Component, Host, Prop, h } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { BsImage } from '../../atoms/image/image';
import { Breakpoint, compareBreakpoint } from '../../../utils/breakpoints';
import { IconName } from '../../atoms/icon/icon.types';
import { TextSize } from '../../atoms/text/text.types';

/**
 * Carousel card component.
 */
@Component({
  tag: 'bs-carousel-card',
  styleUrl: 'carousel-card.scss',
  shadow: true,
})
export class BsCarouselCard {
  /** Props */
  /**
   * The list of images to be shown in the carousel.
   */
  @Prop() imageCollection: BsImage[] | string;

  @Memoize('imageCollection')
  get parsedImageCollection(): BsImage[] {
    const parsed = parseIfString(this.imageCollection) as BsImage[];
    const length = compareBreakpoint('min', Breakpoint.md) && !this.slim ? 4 : 3;

    return parsed.slice(0, length);
  }

  /**
   * Should autoplay be enabled?
   */
  @Prop() autoplay?: boolean = false;

  /**
   * Should the autoplay be slow?
   */
  @Prop() slow?: boolean = false;

  /**
   * The title of the carousel displayed in the footer.
   */
  @Prop() carouselTitle?: string;

  /**
   * The logo to be displayed in the footer.
   */
  @Prop() logo?: BsImage | string;

  /**
   * The cta label.
   */
  @Prop() cta: string;

  /**
   * Should the carousel be displayed in slim variation.
   */
  @Prop() slim?: boolean = false;

  render() {
    const carouselData = {
      logo: this.logo,
      carouselTitle: this.carouselTitle,
      autoplay: this.autoplay,
      slow: this.slow,
      imageCollection: this.imageCollection,
    };

    return (
      <Host class={this.slim ? 'is-slim' : ''}>
        <div class="carousel-card">
          {this.parsedImageCollection.map((image, index) => (
            <bs-click-action
              name="open-carousel-modal"
              value={{ index, ...carouselData }}
              class="card-item"
            >
              <bs-image
                class="card-image"
                src={image.src}
                srcset={image.srcset}
                width={image.width}
                height={image.height}
                imageSizes="200px, 400px"
                altText={image.altText}
                itemprop={image.itemprop}
                cover={true}
              />
            </bs-click-action>
          ))}

          <bs-click-action
            name="open-carousel-modal"
            class="card-item"
            value={{ index: 0, ...carouselData }}
            tagClass="button-square"
          >
            <span class="icon">
              <bs-icon name={IconName.Gallery} />
            </span>
            <bs-text text={this.cta} size={TextSize.Default} />
          </bs-click-action>
        </div>
      </Host>
    );
  }
}
