:host {
  display: flex;
  height: 100%;
}

:host .carousel-card {
  display: flex;
  flex: 1;
  flex-flow: wrap;
  gap: rem(7);
  padding: rem(10);
  border: 1px solid $gray-800;
  border-radius: 6px;
}

:host .card-item {
  position: relative;
  display: flex;
  flex: 1 1 0;
  flex-direction: column;
  gap: rem(8);
  align-items: center;
  justify-content: center;
  width: 103px;
  height: 77px;
  overflow: hidden;
  border: 1px transparent;
  border-radius: 4px;

  @include mq('min', 'md') {
    flex: 1 0 0%;
    width: 144px;
    height: 108px;
  }

  &:last-child {
    display: flex;
    flex: 1 0 100%;
    height: rem(48);
    border: 0;

    .cover {
      display: flex;
      flex-direction: row;
      gap: rem(8);
      align-items: center;
      justify-content: center;
    }

    .cta-icon {
      font-size: rem(24);
    }

    @include mq('min', 'md') {
      flex: unset;
      height: rem(108);

      .cover {
        flex-direction: column;
      }
    }
  }
}

:host .button-square {
  --button-bg-color: #{$white};
  --button-text-color: #{$black};
  --button-border-color: #{$red-100};
  display: flex;
  gap: rem(8);
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  color: var(--button-text-color);
  background-color: var(--button-bg-color);
  border: 1px solid var(--button-border-color);
  border-radius: 4px;

  &:hover {
    --button-bg-color: #{$red-100};
    --button-border-color: #{$red-100};
    --button-text-color: #{$white};
  }

  &:active {
    --button-bg-color: #{$red-200};
    --button-border-color: #{$red-200};
    --button-text-color: #{$white};
  }

  @include mq('min', 'md') {
    flex-direction: column;
  }

  .icon {
    font-size: rem(24);
  }
}

:host(.is-slim) {
  .card-item {
    flex: 1 1 0;
    min-width: 103px;
    height: 130px;

    &:last-child {
      flex: 1 0 100%;
      align-self: flex-end;
      height: rem(48);

      .cover {
        flex-direction: row;
      }
    }
  }

  .button-square {
    flex-direction: row;
  }
}
